<template>
  <div id="app">
        
<div id="breadApp">
                <!-- 主要内容 -->
                <div class="bread-header">
                  <div class="left" style="float:left;margin-left:25px; line-height:54px"> 
                    <span style="color:gray">报告管理 / <label for="" style="color:black">参数配置</label> </span>
                  </div>
                  
                   <div class="right2" style="position:absolute;right:100px;top:60px;">
                   <el-form ref="form" :model="form" label-width="80px"  >
                    <el-button type="primary"><router-link to='./Filelist' style="height:32px; width:65px; color:white;">返回 </router-link></el-button>
                   </el-form>
                
                </div>
                </div>
</div>

        <div class="card2">
           
               <div class="card2_top">
                 <el-form :inline="true" :model="form" class="demo-form-inline">
                  <div style="float: left; margin-left:10%; margin-top: 20px;">
                      <el-form-item label="参数名称" >
                          <el-input v-model="form.powerPlantName" placeholder="请输入" id="btn0"></el-input>
                        </el-form-item>
                       
                        <el-form-item>
                          <el-button type="primary" @click='search()'>查询</el-button>
                        </el-form-item>
                        <el-form-item>
                          <el-button type="info" @click='reback'>重置</el-button>
                        </el-form-item>
                  </div>
                   <div style="float: right; margin-right:10%; margin-top: 20px;">
                      <el-form-item>
                          <el-button type="success" @click='add'><router-link to='./canshuKong'> +新增</router-link></el-button>
                        </el-form-item>
                   </div>
                 </el-form>
               </div>
               <div id="card2_bottom">
                 <el-table :data="list.slice((currpage - 1) * pagesize, currpage * pagesize)" style="width: 100%"
                   @selection-change="handleSelectionChange" >
                   <el-table-column prop="powerPlantName" label="参数名称">
                   </el-table-column>
                   <el-table-column prop="powerPlantType" label="参数类型">
                   </el-table-column>
                  
                   <el-table-column label="操作">
                     <template slot-scope="scope">
                       <el-button @click="handleClick(scope.row)" type="text" size="small" :data='list.id' >删除
                       </el-button>
                       <el-button @click="handleClick2(scope.row),Show()" type="text" size="small" >修改
                       </el-button>
                     </template>
                   </el-table-column>
                 </el-table>
                 <el-pagination background layout="prev, pager, next, sizes, total, jumper" :page-sizes="[5, 10, 15, 20]"
                   :page-size="pagesize" :total="list.length" @current-change="handleCurrentChange"
                   @size-change="handleSizeChange">
                 </el-pagination>
               </div>
             </div>
             
  </div>

  
</template>

<script>
export default {
  name: '',
  data() { 
    return {
        isShow:false,
        
          form: {
            name: '',
            region: '',
  
          },
          formInline: {
            user: '',
            region: ''
          },
        powerPlantType: '',
        generatorCapacity: '',
        voltageLevel: '',
        province: '',
        form: {
          powerPlantName: '',
          address: '',
          phone: '',
          postcode: '',
        },
        msg: 8888,
        list3:[

        ],
        list2:[

        ],
        list:[
            {
                powerPlantName: '高压对低压及绝缘电阻R15s',
                powerPlantType: '定量',
               
            },
            {
              powerPlantName: '高压对低压及绝缘电阻R60s',
                powerPlantType: '定量',
             
               
            }
        ],
        pagesize: 5,
        currpage: 1
      }
  }
 }
</script>

<style lang="" scoped>
 .back{
    position: absolute;
    width: 100%;
    height: 100%;
    background: black;
    background-color:rgba(0,0,0,0.5);
    z-index: 2;
  }
  .el-input {
    width: 150px;
    height: 25px;
  }

  .el-select {
    width: 150px;
    height: 25px;
  }

  .el-form-item {
    margin-bottom: 10px;
  }

 .el-button {
    padding: 10px 20px;
  }


  .card2 {
    left: 1.5%;
    height: 85%;
    text-align: center;
    width: 97%;
    position: absolute;
    background: white;
    top: 120px;
  }

  .card1 {
    width: 550px;
    padding: 10px;
    margin: 0 auto;
    text-align: center;
    background: white;
  }
  .el-form-item__label{
    width: 100px !important;
    padding: 0 !important;
  }
  .card1 .el-input {
    width: 400px;
    height: 30px;
  }

  .card1 .el-select {
    width: 400px;
    height: 25px;
  }


  .card1 .el-button {
    padding: 8px 16px;
  }

  .element.style{
    width: 120px;
  }

  #breadApp .bread-header {
  width: 100%;
  height: 54px;
  background: rgba(255, 255, 255, 1);
  /* border: 1px solid #000; */
}


#breadApp .el-button {
    padding: 8px 16px;
  }
#breadApp .mainContent {
  /* height: 815px; */
  width: 97.5%;
  background: rgba(255, 255, 255, 1);
  /* border: 1px solid #000; */
  margin-top: 14px;
  margin-left: 1.25%;
  border-radius: 2px;
}

#breadApp .el-breadcrumb {
  margin-left: 32px;
}

#breadApp .el-breadcrumb span {
  line-height: 54px;
}

#breadApp .el-breadcrumb__item:last-child .el-breadcrumb__inner {
  font-weight: 400;
  color: rgba(51, 51, 51, 1);
}

#breadApp .el-breadcrumb__item:first-child {
  color: rgba(136, 136, 136, 1);
}

</style>


